"use client";

import { useEffect, useState } from "react";

type ReportRow = {
  engagement: string;
  activity: string;
  budget: number;
  actual: number;
  activityId: string;
};

export default function BudgetReportPage() {
  const [reportData, setReportData] = useState<ReportRow[]>([]);
  const [selectedEngagement, setSelectedEngagement] = useState<string>("");

  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("/api/fetchBudgetAndActuals");
      const data = await res.json();
      setReportData(data);
    };
    fetchData();
  }, []);

  const engagements = Array.from(
    new Set(reportData.map((row) => row.engagement))
  );

  const filteredData = reportData
    .filter((row) => row.engagement === selectedEngagement)
    .sort((a, b) => a.activityId.localeCompare(b.activityId, "ja", { numeric: true }));

  const totalBudget = filteredData.reduce((sum, row) => sum + row.budget, 0);
  const totalActual = filteredData.reduce((sum, row) => sum + row.actual, 0);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">📊 予実レポート</h1>

      <div className="mb-6">
        <label className="mr-3 font-semibold text-gray-700">
          Engagement（会社）を選択：
        </label>
        <select
          value={selectedEngagement}
          onChange={(e) => setSelectedEngagement(e.target.value)}
          className="border px-3 py-2 rounded-md shadow-sm"
        >
          <option value="">-- 選択してください --</option>
          {engagements.map((eng) => (
            <option key={eng} value={eng}>
              {eng}
            </option>
          ))}
        </select>
      </div>

      {selectedEngagement && (
        <>
          <h2 className="text-xl font-semibold mb-3">
            「{selectedEngagement}」の予算・実績一覧
          </h2>
          <div className="overflow-x-auto">
            <table className="min-w-full text-sm border-collapse border">
              <thead className="bg-gray-100">
                <tr>
                  <th className="border px-4 py-2">Activity ID</th>
                  <th className="border px-4 py-2">Activity</th>
                  <th className="border px-4 py-2">予算時間</th>
                  <th className="border px-4 py-2">実績時間</th>
                  <th className="border px-4 py-2">差分</th>
                </tr>
              </thead>
              <tbody>
                {filteredData.map((row, idx) => {
                  const diff = row.actual - row.budget;
                  return (
                    <tr key={idx} className="text-center">
                      <td className="border px-4 py-2">{row.activityId}</td>
                      <td className="border px-4 py-2">{row.activity}</td>
                      <td className="border px-4 py-2">{row.budget} h</td>
                      <td className="border px-4 py-2">{row.actual} h</td>
                      <td
                        className="border px-4 py-2 font-bold"
                        style={{
                          color: diff > 0 ? "red" : diff < 0 ? "green" : "black",
                        }}
                      >
                        {(diff >= 0 ? "+" : "") + diff.toFixed(2)} h
                      </td>
                    </tr>
                  );
                })}

                {/* ✅ 合計行 */}
                <tr className="bg-gray-100 font-semibold text-center">
                  <td className="border px-4 py-2"></td>
                  <td className="border px-4 py-2">合計</td>
                  <td className="border px-4 py-2">{totalBudget.toFixed(2)} h</td>
                  <td className="border px-4 py-2">{totalActual.toFixed(2)} h</td>
                  <td
                    className="border px-4 py-2"
                    style={{
                        color:
                          totalActual - totalBudget > 0
                          ? "red"
                          : totalActual - totalBudget < 0
                          ? "green"
                          : "black",
                    }}
                >
                  {(totalActual - totalBudget >= 0 ? "+" : "") +
                     (totalActual - totalBudget).toFixed(2)}{" "}
                  h
                </td>
               </tr>
              </tbody>
            </table>
          </div>
        </>
      )}
    </div>
  );
}
