"use client";
import { useEffect, useState } from "react";

interface ReportRow {
  userId: string;
  userName: string;
  engagement: string;
  activity: string;
  budget: number;
  actual: number;
}

export default function UserReportPage() {
  const [data, setData] = useState<ReportRow[]>([]);
  const [selectedEngagement, setSelectedEngagement] = useState<string>("");
  const [selectedUser, setSelectedUser] = useState<string>("");

  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("/api/fetchUserBudgetActuals");
      if (res.ok) {
        const json = await res.json();
        setData(json);
      }
    };
    fetchData();
  }, []);

  const uniqueEngagements = Array.from(new Set(data.map(d => d.engagement)));
  const uniqueUsers = Array.from(new Set(data.map(d => d.userName)));

  const filteredData = data.filter(row => {
    const matchEngagement = selectedEngagement ? row.engagement === selectedEngagement : true;
    const matchUser = selectedUser ? row.userName === selectedUser : true;
    return matchEngagement && matchUser;
  });

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">👤 ユーザー別予実レポート</h1>

      <div className="flex flex-wrap gap-4 mb-6">
        <div>
          <label className="mr-2 font-semibold">Engagement:</label>
          <select
            value={selectedEngagement}
            onChange={(e) => setSelectedEngagement(e.target.value)}
            className="border p-2 rounded"
          >
            <option value="">-- 全て --</option>
            {uniqueEngagements.map((e) => (
              <option key={e} value={e}>{e}</option>
            ))}
          </select>
        </div>

        <div>
          <label className="mr-2 font-semibold">User:</label>
          <select
            value={selectedUser}
            onChange={(e) => setSelectedUser(e.target.value)}
            className="border p-2 rounded"
          >
            <option value="">-- 全て --</option>
            {uniqueUsers.map((u) => (
              <option key={u} value={u}>{u}</option>
            ))}
          </select>
        </div>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full text-sm border-collapse border">
          <thead className="bg-gray-100">
            <tr>
              <th className="border px-4 py-2">ユーザー</th>
              <th className="border px-4 py-2">Engagement</th>
              <th className="border px-4 py-2">Activity</th>
              <th className="border px-4 py-2">予算</th>
              <th className="border px-4 py-2">実績</th>
              <th className="border px-4 py-2">差分</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((row, idx) => {
              const diff = row.actual - row.budget;
              return (
                <tr key={idx} className="text-center">
                  <td className="border px-4 py-2">{row.userName}</td>
                  <td className="border px-4 py-2">{row.engagement}</td>
                  <td className="border px-4 py-2">{row.activity}</td>
                  <td className="border px-4 py-2">{row.budget} h</td>
                  <td className="border px-4 py-2">{row.actual} h</td>
                  <td
                    className="border px-4 py-2 font-semibold"
                    style={{ color: diff > 0 ? "red" : diff < 0 ? "green" : "black" }}
                  >
                    {(diff >= 0 ? "+" : "") + diff.toFixed(2)} h
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}
